:root {
  --firstFontColor: #212121;
  --secondFontColor: #757575;
  --lightFontColor: #fff;
  --brandColor: #2196f3;
  --brandHover: #188ce8;
  --secondBgColor: #f5f4fa;
  --darkBgColor: #2f303a;
  --cubic: cubic-bezier(.4, 0, .2, 1);
}

html {
  scroll-behavior: smooth;
}

body {
  letter-spacing: .03em;
  color: var(--firstFontColor);
  background-color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: normal;
  line-height: 1;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  border: 0;
}

a {
  text-decoration: none;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.logo {
  color: var(--brandColor);
  font-family: Raleway;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.666;
  display: block;
}

@media screen and (min-width: 768px) {
  .logo {
    font-size: 24px;
    line-height: 1.17;
  }
}

@media screen and (min-width: 1200px) {
  .logo {
    font-size: 26px;
    line-height: 1.19;
    animation: logoWayver 5s linear infinite alternate;
  }
}

.logo__text {
  color: var(--lightFontColor);
}

.logo__text--dark {
  color: var(--firstFontColor);
}

@keyframes logoWayver {
  0% {
    transform: scale(.8);
  }

  25% {
    transform: scale(1.1);
  }

  50% {
    transform: scale(.9);
  }

  75% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.mobile-menu {
  flex-grow: 1;
  display: flex;
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    align-items: center;
  }
}

@media screen and (max-width: 767.5px) {
  .mobile-menu {
    width: 100vw;
    height: 100vh;
    opacity: 0;
    background-color: #fff;
    flex-direction: column;
    padding: 48px 15px 48px 40px;
    transition: transform .25s ease-in-out, opacity .25s linear;
    position: fixed;
    top: 0;
    left: 0;
    transform: translateX(100%);
  }
}

.icon-is-hidden {
  display: none;
}

.mobile-menu--is-open {
  opacity: 1;
  transform: translateX(0);
}

.menu {
  display: block;
}

@media screen and (min-width: 768px) {
  .menu {
    display: flex;
  }
}

.menu__item {
  position: relative;
}

@media screen and (max-width: 767.5px) {
  .menu__item:not(:last-child) {
    margin-bottom: 32px;
  }
}

@media screen and (min-width: 768px) {
  .menu__item:not(:last-child) {
    margin-right: 50px;
  }
}

.menu__link {
  letter-spacing: .02em;
  color: var(--firstFontColor);
  font-size: 40px;
  font-weight: 500;
  line-height: 1.175;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.menu__link:hover, .menu__link:focus {
  color: var(--brandColor);
}

@media screen and (min-width: 768px) {
  .menu__link {
    text-align: center;
    padding: 32px 0;
    font-size: 14px;
    line-height: 1.14;
  }
}

@media screen and (min-width: 768px) {
  .menu__link--active {
    color: var(--brandColor);
  }

  .menu__link--active:after {
    content: "";
    width: 100%;
    height: 4px;
    background-color: var(--brandColor);
    border-radius: 2px;
    display: block;
    position: absolute;
    bottom: -1px;
    left: 0;
  }
}

@media screen and (min-width: 1200px) {
  .contacts {
    margin-left: auto;
  }
}

@media screen and (min-width: 1200px) {
  .contacts {
    display: flex;
  }
}

.contacts__item:not(:last-child) {
  margin-bottom: 32px;
}

@media screen and (min-width: 768px) {
  .contacts__item:not(:last-child) {
    margin-bottom: 10px;
  }
}

@media screen and (min-width: 1200px) {
  .contacts__item:not(:last-child) {
    margin-bottom: 0;
    margin-right: 50px;
  }
}

.contacts__link {
  letter-spacing: .02em;
  color: var(--secondFontColor);
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
  display: block;
}

@media screen and (min-width: 768px) {
  .contacts__link {
    align-items: center;
    font-size: 12px;
    line-height: 1.14;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
  }

  .contacts__link:hover, .contacts__link:focus {
    color: var(--brandColor);
  }
}

@media screen and (min-width: 1200px) {
  .contacts__link {
    padding: 32px 0;
    font-size: 14px;
  }
}

@media screen and (min-width: 450px) and (max-width: 767.5px) {
  .contacts__link--big-brand {
    color: var(--brandColor);
    font-size: 34px;
    line-height: 1.176;
  }
}

.contacts__icon {
  fill: currentColor;
  display: none;
}

@media screen and (min-width: 768px) {
  .contacts__icon {
    display: block;
  }
}

.contacts__icon--left {
  margin-right: 10px;
}

.mobile-social {
  display: flex;
}

@media screen and (max-width: 419px) {
  .mobile-social {
    flex-wrap: wrap;
  }
}

@media screen and (min-width: 768px) {
  .mobile-social {
    display: none;
  }
}

@media screen and (max-width: 419px) {
  .mobile-social__item {
    flex-basis: 45%;
  }

  .mobile-social__item:nth-child(n+3) {
    margin-top: 20px;
  }
}

@media screen and (min-width: 420px) {
  .mobile-social__item:not(:last-child) {
    border-right: 1px solid rgba(33, 33, 33, .2);
    margin-right: 10px;
    padding-right: 10px;
  }
}

.mobile-social__link {
  letter-spacing: .02em;
  color: var(--brandColor);
  font-size: 18px;
  font-weight: 500;
  line-height: 1.22;
  text-decoration: none;
}

.button {
  width: 200px;
  height: 50px;
  letter-spacing: .06em;
  color: var(--lightFontColor);
  background-color: var(--brandColor);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.button:hover, .button:focus {
  background-color: var(--brandHover);
}

.filter {
  justify-content: center;
  display: flex;
}

.filter__item:not(:last-child) {
  margin-right: 8px;
}

.filter__btn {
  background-color: #f5f4fa;
  border-radius: 4px;
  padding: 6px 22px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

.filter__btn:hover, .filter__btn:focus, .filter__btn:active {
  background-color: var(--brandColor);
  color: #fff;
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.product {
  flex-wrap: wrap;
  margin: -15px;
  display: flex;
}

.product__item {
  flex-basis: 370px;
  margin: 15px;
}

.product__link {
  color: var(--firstFontColor);
  text-decoration: none;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.product__link:focus, .product__link:hover {
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.product__link:hover .product__overlay, .product__link:focus .product__overlay {
  opacity: 1;
  transform: translateY(0);
}

.product__thumb {
  position: relative;
  overflow: hidden;
}

.product__overlay {
  width: 100%;
  height: 100%;
  opacity: 0;
  color: var(--lightFontColor);
  transition: transform 250ms var(--cubic), opacity 250ms linear;
  background-color: rgba(33, 150, 243, .9);
  align-items: center;
  padding: 0 24px;
  font-size: 18px;
  line-height: 1.56;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(100%);
}

.product__box {
  border: 1px solid #eee;
  border-top: none;
  padding: 20px 24px;
}

.product__heading {
  letter-spacing: .06em;
  margin-bottom: 4px;
  font-size: 18px;
  font-weight: 700;
  line-height: 2;
}

.product__text {
  color: var(--secondFontColor);
  font-size: 16px;
  line-height: 1.88;
}

.address {
  font-style: normal;
}

.address__item:not(:last-child) {
  margin-bottom: 9px;
}

.address__link {
  color: var(--secondFontColor);
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.address__link:hover, .address__link:focus {
  color: var(--brandColor);
}

.address__map {
  color: var(--lightFontColor);
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.address__map:hover, .address__map:focus {
  color: var(--brandColor);
}

.social {
  justify-content: center;
  display: flex;
}

.social__item {
  width: 44px;
  height: 44px;
}

.social__item:not(:last-child) {
  margin-right: 10px;
}

.social__link {
  width: 100%;
  height: 100%;
  color: var(--secondFontColor);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.social__link:hover, .social__link:focus {
  background-color: var(--brandColor);
  color: var(--lightFontColor);
}

.social__link--light {
  color: var(--lightFontColor);
  background-color: rgba(255, 255, 255, .1);
}

.social__icon {
  fill: currentColor;
}

.signup {
  display: flex;
}

.signup__input {
  width: 358px;
  height: 50px;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .15));
  color: var(--lightFontColor);
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(255, 255, 255, .3);
  border-radius: 4px;
  outline: none;
  margin-right: 12px;
  padding: 15px 16px;
  font-size: 16px;
  line-height: 1.25;
}

.signup__input:focus {
  border-color: var(--brandColor);
}

.signup__input::-ms-input-placeholder {
  color: rgba(255, 255, 255, .6);
  font-size: 16px;
  line-height: 1.25;
}

.signup__input::placeholder {
  color: rgba(255, 255, 255, .6);
  font-size: 16px;
  line-height: 1.25;
}

.signup__icon {
  fill: currentColor;
}

.signup__icon--right {
  margin-left: 10px;
}

.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 200;
  transition: opacity 250ms var(--cubic), visibility 250ms var(--cubic);
  background-color: rgba(0, 0, 0, .2);
  padding: 15px;
  position: fixed;
  top: 0;
  left: 0;
}

@media screen and (min-width: 480px) {
  .backdrop {
    justify-content: center;
    align-items: center;
    display: flex;
  }
}

.modal {
  opacity: 1;
  transition: opacity 250ms var(--cubic), transform 250ms var(--cubic);
  background-color: #fff;
  border-radius: 4px;
  padding: 40px;
  position: relative;
  transform: scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 480px) {
  .modal {
    width: 450px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
    height: 581px;
  }
}

.backdrop.backdrop--is-hidden .modal {
  transform: scale(1.4);
}

.modal__close {
  width: 30px;
  height: 30px;
  color: var(--firstFontColor);
  transition: color 250ms var(--cubic), border-color 250ms var(--cubic);
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__close:hover, .modal__close:focus {
  color: var(--brandColor);
  border-color: var(--brandColor);
}

.modal__icon {
  fill: currentColor;
}

.backdrop--is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.overflow {
  overflow: hidden;
}

.modal__text {
  text-align: center;
  margin-bottom: 12px;
  font-size: 15px;
  font-weight: 700;
  line-height: 1.15;
}

@media screen and (min-width: 480px) {
  .modal__text {
    font-size: 20px;
  }
}

.form__field {
  letter-spacing: .01em;
  color: var(--secondFontColor);
  font-size: 12px;
  line-height: 1.166;
  display: block;
}

.form__field:not(:last-child) {
  margin-bottom: 10px;
}

.form__field:nth-child(4) {
  margin-bottom: 20px;
}

.form__wrapper {
  margin-top: 4px;
  display: block;
  position: relative;
}

.form__input {
  width: 100%;
  height: 40px;
  transition: border-color 250ms var(--cubic);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  padding: 11px 12px 11px 42px;
  display: block;
}

.form__field:focus-within .form__input, .form__field:focus-within .form__textarea {
  border-color: var(--brandColor);
}

.form__field:focus-within .form__icon {
  fill: var(--brandColor);
}

.form__textarea {
  width: 100%;
  height: 120px;
  resize: none;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  margin-top: 4px;
  padding: 12px 16px;
  display: block;
}

.form__textarea::-ms-input-placeholder {
  letter-spacing: .01em;
  color: rgba(117, 117, 117, .5);
  font-size: 14px;
  line-height: 1.14;
}

.form__textarea::placeholder {
  letter-spacing: .01em;
  color: rgba(117, 117, 117, .5);
  font-size: 14px;
  line-height: 1.14;
}

.form__icon {
  transition: fill 250ms var(--cubic);
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

.form__label {
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  display: flex;
}

.form__checkbox {
  min-width: 16px;
  height: 15px;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  transition: background-color 250ms var(--cubic), border-color 250ms var(--cubic);
  background-color: rgba(0, 0, 0, 0);
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='13' height='10' viewBox='0 0 13 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.95703 4.75166L1.88825 4.68604L1.81923 4.75141L0.93123 5.59258L0.854858 5.66492L0.930974 5.73753L4.42671 9.07236L4.49574 9.1382L4.56476 9.07236L12.069 1.91352L12.1449 1.84116L12.069 1.76881L11.1873 0.927644L11.1183 0.861826L11.0493 0.927611L4.49577 7.17353L1.95703 4.75166Z' fill='white' stroke='white' stroke-width='0.2'/%3E%3C/svg%3E");
  background-position: center;
  background-repeat: no-repeat;
  border: 2px solid #212121;
  border-radius: 2px;
  margin-right: 7px;
  display: block;
}

.form__checkbox:checked {
  background-color: var(--brandColor);
  border-color: var(--brandColor);
  background-origin: border-box;
}

.form__agreement {
  color: var(--secondFontColor);
  font-size: 11px;
  line-height: 1.16;
  display: block;
}

@media screen and (min-width: 1200px) {
  .form__agreement {
    font-size: 14px;
    line-height: 1.71;
  }
}

.form__link {
  color: var(--brandColor);
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
}

.form__button {
  margin: 0 auto;
}

